import React, {Component} from 'react';
import {connect} from 'react-redux';
import {CircularProgress} from '@material-ui/core';
import './assets/css/style.css';

class Loading extends Component {
    render () {
        if (!this.props.progress_bar.loading) {
            return "";
        }
        
        return (
            <div className="loading-wrapper">
                <CircularProgress className="loading-progress-bar" />
            </div>
        );
    }
}

const mapStateToProps = state => ({
    content: state.content,
    offers_history: state.offers_history,
    progress_bar: state.progress_bar,
    general: state.general
});

const mapsDispatchToProps = dispatch => {
    return {}
}

export default connect(mapStateToProps, mapsDispatchToProps)(Loading);