import {getDefaultValue, setDefaultValue} from './storage';

const actions = {
    OFFERS_HISTORY_USED_RANGE: "ohst1",
    OFFERS_HISTORY_NEW_RANGE: "ohst2",
    OFFERS_HISTORY_VISIBLE: "ohst3",
    OFFERS_HISTORY_LINK_VISIBLE: "ohst4"
}

const initialState = {
    used_offers: [],
    new_offers: [],
    offers_history_visible: getDefaultValue('offers_history_visible', false),
    offers_history_link_visible: true,
}


const offersHistoryReducer = (state=initialState, action) => {
    switch(action.type) {
        case actions.OFFERS_HISTORY_USED_RANGE:
            return {
                ...state,
                used_offers: action.payload
            }

        case actions.OFFERS_HISTORY_NEW_RANGE:
            return {
                ...state,
                new_offers: action.payload
            }
        case actions.OFFERS_HISTORY_VISIBLE:
            setDefaultValue('offers_history_visible', !state.offers_history_visible);
            return {
                ...state,
                offers_history_visible: !state.offers_history_visible
            }
        case actions.OFFERS_HISTORY_LINK_VISIBLE:
            return {
                ...state,
                offers_history_link_visible: action.payload
            }
        default:
            return state;
    }
}


export default offersHistoryReducer;
export {
    actions
}