const actions = {
    TRACK_PRODUCT_ISBN: "trp1",
    TRACK_PRODUCT_PRICE: "trp2",
    TRACK_PRODUCT_CONDITION: "trp3",
    TRACK_PRODUCT_DESCRIPTION: "trp4",
    TRACK_PRODUCT_SELLER_NAME: "trp5",
    TRACK_PRODUCT_SELLER_RATING: "trp6",
    TRACK_PRODUCT_SELLER_RATING_PERCENT: "trp7",
    TRACK_PRODUCT_SHIP_US: "trp8",
    TRACK_PRODUCT_IS_SET: "trp9",
    TRACK_PRODUCT_SITE: "trp10",
}

let initialState = {
    isSetTracking: false,
    isbn: "",
    price: "",
    condition: [],
    site: [],
    description: [],
    seller_name: [],
    seller_rating: "",
    seller_rating_percent: "",
    ships_from_us_only: false,
}

const trackProductReducer = (state = initialState, action) => {
    switch (action.type) {
        case actions.TRACK_PRODUCT_ISBN:
            return {
                ...state,
                isbn: action.payload
            }
        case actions.TRACK_PRODUCT_PRICE:
            return {
                ...state,
                price: action.payload
            }
        case actions.TRACK_PRODUCT_CONDITION:
            return {
                ...state,
                condition: action.payload
            }
        case actions.TRACK_PRODUCT_DESCRIPTION:
            return {
                ...state,
                description: action.payload
            }
        case actions.TRACK_PRODUCT_SELLER_NAME:
            return {
                ...state,
                seller_name: action.payload
            }
        case actions.TRACK_PRODUCT_SELLER_RATING:
            return {
                ...state,
                seller_rating: action.payload
            }
        case actions.TRACK_PRODUCT_SELLER_RATING_PERCENT:
            return {
                ...state,
                seller_rating_percent: action.payload
            }
        case actions.TRACK_PRODUCT_SHIP_US:
            return {
                ...state,
                ships_from_us_only: action.payload
            }
        case actions.TRACK_PRODUCT_IS_SET:
            return {
                ...state,
                isSetTracking: action.payload
            }
        case actions.TRACK_PRODUCT_SITE:
            return {
                ...state,
                site: action.payload
            }
        default:
            return state;
    }
}

export default trackProductReducer;
export {
    actions
}