import React, {Component} from 'react';
import {connect} from 'react-redux';
import {Dialog, DialogTitle, DialogContentText, DialogContent, DialogActions, Button} from '@material-ui/core';
import {actions} from '../../actions';


class Alert extends Component {
    render () {
        return (
            <Dialog 
            data-test="dialog"
            open={this.props.alert.open} 
            onClose={e => {}} 
            aria-labelledby="alert-dialog-title" 
            aria-describedby="alert-dialog-description">
                <DialogTitle id="alert-dialog-title">{this.props.alert.title}</DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        {this.props.alert.context_text}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={e => this.props.set_open(false)} color="primary" autoFocus>Close</Button>
                </DialogActions>
            </Dialog>
        )
    }
}

const mapStateToProps = state => {
    return {
        alert: state.alert
    }
}

const mapDispatchToProps = dispatch => {
    return {
        set_title: payload => dispatch({type: actions.ALERT_TITLE, payload}),
        set_context_text: payload => dispatch({type: actions.ALERT_CONTEXT_TEXT, payload}),
        set_open: payload => dispatch({type: actions.ALERT_OPEN, payload})
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Alert);