import config from './config.json';
import axios from 'axios';
import SendPostMessage from './send_post_message';

class ApiFetcher {
    static ADDITIONAL_CATEGORY_USED_LIKE_NEW = 1;
    static ADDITIONAL_CATEGORY_USED_VERY_GOOD = 2;
    static ADDITIONAL_CATEGORY_USED_GOOD = 3;
    static ADDITIONAL_CATEGORY_USED_ACCEPTABLE = 4;
    static ADDITIONAL_CATEGORY_HISTORICAL_DATA = 5;
    static ADDITIONAL_CATEGORY_WAREHOUSE = 6;
    static TIMEOUT = 10000; // 10 seconds

    static API_CREDENTIALS = true; // omit, same-origin, include
    static CORS_MODE = "cors"; // no-cors, cors, same-origin

    static API_URL = config.API_PROD_DEVEL_MODE ? config.API_PROD_DEVEL_URL : (config.API_DEVEL_MODE ? (config.API_DEVEL_LOCAL ? config.API_DEVELOPMENT_URL_LOCAL : config.API_DEVELOPMENT_URL) : config.API_PRODUCTION_URL);

    static redirectToLoginPage(isbn) {
        window.location.replace(`https://membercentral.net/app/ext${config.API_PROD_DEVEL_MODE ? 'dev' : ''}/auth?asin=${isbn}`)
    }

    static async getPriceHistory(isbn) {
        return new Promise(async (resolve, reject) => {
            let url = (ApiFetcher.API_URL + config.API_ENDPOINT_PRICE_HISTORY).replace("{{isbn}}", isbn);

            try {
                var response = await axios.get(url, {
                    timeout: ApiFetcher.TIMEOUT,
                    // credentials: ApiFetcher.API_CREDENTIALS,
                    withCredentials: true
                });
            } catch (error) {
                var response = error.response;
            }

            switch (response && response.status && response.data.status) {
                case 200:
                    resolve({
                        ...response.data.data,
                        tracking: response.data.tracking
                    });
                    SendPostMessage.ifPossibleThenSend((tunnel) => {
                        tunnel.send("reveal_stock");
                    });
                    break;
                case 401:
                    ApiFetcher.redirectToLoginPage(isbn);
                    break;
                default:
                    if (response && response.data && response.data.error_message) {
                        response.data.error_message = "Could not get price history. Refresh the page!"
                        reject(response.data.data);
                    } else {
                        reject({ error_message: 'Something went wrong' });
                    }
            }
        });
    }

    static async getPriceHistoryAdditional(isbn, category) {
        return new Promise(async (resolve, reject) => {
            let url = (ApiFetcher.API_URL + config.API_ENDPOINT_PRICE_HISTORY_ADDITIONAL).replace("{{isbn}}", isbn).replace("{{category}}", category);

            try {
                var response = await axios.get(url, {
                    timeout: ApiFetcher.TIMEOUT,
                    // credentials: ApiFetcher.API_CREDENTIALS,
                    withCredentials: true
                });
            } catch (error) {
                var response = error.response;
            }

            switch (response && response.status && response.data.status) {
                case 200:
                    resolve({
                        ...response.data.data,
                        tracking: response.data.tracking
                    });
                    break;
                case 401:
                    ApiFetcher.redirectToLoginPage(isbn);
                    break;
                default:
                    if (response) {
                        response.data.error_message = "Could not get additional historical data. Refresh the page!"
                        reject(response.data.data);
                    } else {
                        reject({ error_message: 'Something went wrong' });
                    }
            }
        });
    }

    static async getTrackProduct(isbn) {
        return new Promise(async (resolve, reject) => {
            let url = (ApiFetcher.API_URL + config.API_ENDPOINT_TRACK_PRODUCT).replace("{{isbn}}", isbn);
            try {
                var response = await axios.get(url, {
                    timeout: ApiFetcher.TIMEOUT,
                    // credentials: ApiFetcher.API_CREDENTIALS,
                    withCredentials: ApiFetcher.API_CREDENTIALS
                });
            } catch (error) {
                var response = error.response;
            }

            switch (response && response.status && response.data.status) {
                case 200:
                    resolve(response.data.data);
                    break;
                case 401:
                    ApiFetcher.redirectToLoginPage(isbn);
                    break;
                default:
                    if (response) {
                        response.data.error_message = "Could not get tracking settings. Refresh the page!"
                        reject(response.data.data);
                    } else {
                        reject({ error_message: 'Something went wrong' });
                    }
            }
        })
    }

    static async deleteTrackProduct(isbn) {
        const formData = new FormData();
        formData.append('isbn', isbn);
        
        return new Promise(async (resolve, reject) => {
            try {
                let url = ApiFetcher.API_URL + config.API_ENDPOINT_DELETE_TRACKING_PRODUCT;
                var response = await axios(url, {
                    method: 'POST',
                    cache: 'no-cache',
                    timeout: ApiFetcher.TIMEOUT,
                    // credentials: ApiFetcher.API_CREDENTIALS,
                    withCredentials: ApiFetcher.API_CREDENTIALS,
                    data: formData
                });
            } catch (error) {
                var response = error.response;
            }

            switch (response && response.status && response.data.status) {
                case 200:
                    resolve({
                        data: 'OK'
                    });
                    break;
                case 401:
                    ApiFetcher.redirectToLoginPage(isbn);
                    break;
                default:
                    if (response) {
                        if (response.data.error_message) {
                            response.data.error_message = "Could not update tracking settings. Refresh the page!";
                        }
                        reject(response.data.data);
                    } else {
                        reject({ error_message: 'Something went wrong' });
                    }
            }
        });
    }

    static async updateTrackProduct(items, isbn) {
        const formData = new FormData();

        Object.keys(items).forEach(key => {
            if (typeof items[key] === 'object') {
                Object.keys(items[key]).forEach(key2 => {
                    formData.append(key + "[]", items[key][key2])
                });
            } else {
                formData.append(key, items[key]);
            }
        });

        return new Promise(async (resolve, reject) => {
            try {
                let url = ApiFetcher.API_URL + config.API_ENDPOINT_TRACK_PRODUCT_PUT;
                var response = await axios(url, {
                    method: 'POST',
                    cache: 'no-cache',
                    timeout: ApiFetcher.TIMEOUT,
                    // credentials: ApiFetcher.API_CREDENTIALS,
                    withCredentials: ApiFetcher.API_CREDENTIALS,
                    data: formData
                });
            } catch (error) {
                var response = error.response;
            }

            switch (response && response.status && response.data.status) {
                case 200:
                    resolve({
                        data: response.data.data,
                        alert_count: response.data.alert_count,
                        alert_limit: response.data.alert_limit
                    });
                    break;
                case 401:
                    ApiFetcher.redirectToLoginPage(isbn);
                    break;
                default:
                    if (response) {
                        response.data.error_message = "Could not update tracking settings. Refresh the page!"
                        reject(response.data.data);
                    } else {
                        reject({ error_message: 'Something went wrong' });
                    }
            }
        })
    }
}

export default ApiFetcher;