const actions = {
    ALERT_OPEN: "alrt1",
    ALERT_TITLE: "alrt2",
    ALERT_CONTEXT_TEXT: "alrt3",
}

const initialState = {
    title: "",
    context_text: "",
    open: false
}

const alertReducer = (state=initialState, action) => {
    switch(action.type) {
        case actions.ALERT_OPEN:
            return {
                ...state,
                open: action.payload
            }
        case actions.ALERT_TITLE:
            return {
                ...state,
                title: action.payload
            }
        case actions.ALERT_CONTEXT_TEXT:
            return {
                ...state,
                context_text: action.payload
            }
        default:
            return state
    }
}

export default alertReducer;

export {
    actions
}