import {getDefaultValue, setDefaultValue} from './storage';

const actions = {
    PRICE_HISTORY_AMAZON_VISIBLE: "phst1",
    PRICE_HISTORY_NEW_VISIBLE: "phst2",
    PRICE_HISTORY_USED_ALL_VISIBLE: "phst3",
    PRICE_HISTORY_USED_LIKE_NEW_VISIBLE: "phst4",
    PRICE_HISTORY_USED_VERY_GOOD_VISIBLE: "phst5",
    PRICE_HISTORY_USED_GOOD_VISIBLE: "phst6",
    PRICE_HISTORY_USED_ACCEPTABLE_VISIBLE: "phst7",
    PRICE_HISTORY_SALES_RANK_VISIBLE: "phst8",
    PRICE_HISTORY_AMAZON_RANGE: "phst9",
    PRICE_HISTORY_NEW_RANGE: "phst10",
    PRICE_HISTORY_USED_ALL_RANGE: "phst11",
    PRICE_HISTORY_USED_LIKE_NEW_RANGE: "phst12",
    PRICE_HISTORY_USED_VERY_GOOD_RANGE: "phst13",
    PRICE_HISTORY_USED_GOOD_RANGE: "phst14",
    PRICE_HISTORY_USED_ACCEPTABLE_RANGE: "phst15",
    PRICE_HISTORY_SALES_RANK_RANGE: "phst16",
    PRICE_HISTORY_RANGE_DAY: "phst17",
    PRICE_HISTORY_RANGE_WEEK: "phst18",
    PRICE_HISTORY_RANGE_MONTH: "phst19",
    PRICE_HISTORY_RANGE_3_MONTH: "phst20",
    PRICE_HISTORY_RANGE_YEAR: "phst21",
    PRICE_HISTORY_RANGE_ALL: "phst22",
    PRICE_HISTORY_SALES_RANK_REVERSED: "phst23",
    PRICE_HISTORY_WAREHOUSE_VISIBLE: "phst24",
    PRICE_HISTORY_WAREHOUSE_RANGE: "phst25",
    PRICE_HISTORY_AMAZON_LINE_THICKNESS: "phst26",
    PRICE_HISTORY_NEW_LINE_THICKNESS: "phst27",
    PRICE_HISTORY_USED_LINE_THICKNESS: "phst28",
    PRICE_HISTORY_USED_LIKE_NEW_LINE_THICKNESS: "phst29",
    PRICE_HISTORY_USED_VERY_GOOD_LINE_THICKNESS: "phst30",
    PRICE_HISTORY_USED_GOOD_LINE_THICKNESS: "phst31",
    PRICE_HISTORY_USED_ACCEPTABLE_LINE_THICKNESS: "phst32",
    PRICE_HISTORY_SALES_RANK_LINE_THICKNESS: "phst33",
    PRICE_HISTORY_SALES_RANK_FORECAST_VISIBLE: "phst34"
}

let initialState = {
    amazon_line_thickness: 1,
    new_line_thickness: 1,
    used_line_thickness: 1,
    used_like_new_line_thickness: 6,
    used_very_good_line_thickness: 6,
    used_good_line_thickness: 6,
    used_acceptable_line_thickness: 6,
    sales_rank_line_thickness: 1,
    amazon_visible: getDefaultValue('amazon_visible', true),
    new_visible: getDefaultValue('new_visible', true),
    used_all_visible: getDefaultValue('used_all_visible', true),
    used_like_new_visible: getDefaultValue('used_like_new_visible', false),
    used_very_good_visible: getDefaultValue('used_very_good_visible', false),
    used_good_visible: getDefaultValue('used_good_visible', false),
    used_acceptable_visible: getDefaultValue('used_acceptable_visible', false),
    salesRank_visible: getDefaultValue('salesRank_visible', true),
    salesRank_forecast_visible: getDefaultValue('salesRank_forecast_visible', false),
    warehouse_visible: getDefaultValue('warehouse_visible', false),
    salesRank_reversed: getDefaultValue("salesRank_reversed", false),
    amazon: [],
    newone: [],
    used_all: [],
    used_like_new: [],
    used_very_good: [],
    used_good: [],
    used_acceptable: [],
    salesRank: [],
    warehouse: [],
    range: getDefaultValue('range', actions.PRICE_HISTORY_RANGE_YEAR),

}

const priceHistoryReducer = (state = initialState, action) => {
    switch (action.type) {
        case actions.PRICE_HISTORY_AMAZON_LINE_THICKNESS:
            return {
                ...state,
                amazon_line_thickness: action.payload
            }
        case actions.PRICE_HISTORY_NEW_LINE_THICKNESS:
            return {
                ...state,
                new_line_thickness: action.payload
            }
        case actions.PRICE_HISTORY_USED_LINE_THICKNESS:
            return {
                ...state,
                used_line_thickness: action.payload
            }
        case actions.PRICE_HISTORY_USED_LIKE_NEW_LINE_THICKNESS:
            return {
                ...state,
                used_like_new_line_thickness: action.payload
            }
        case actions.PRICE_HISTORY_USED_VERY_GOOD_LINE_THICKNESS:
            return {
                ...state,
                used_very_good_line_thickness: action.payload
            }
        case actions.PRICE_HISTORY_USED_GOOD_LINE_THICKNESS:
            return {
                ...state,
                used_good_line_thickness: action.payload
            }
        case actions.PRICE_HISTORY_USED_ACCEPTABLE_LINE_THICKNESS:
            return {
                ...state,
                used_acceptable_line_thickness: action.payload
            }
        case actions.PRICE_HISTORY_SALES_RANK_LINE_THICKNESS:
            return {
                ...state,
                sales_rank_line_thickness: action.payload
            }
        case actions.PRICE_HISTORY_AMAZON_VISIBLE:
            setDefaultValue('amazon_visible', !state.amazon_visible);
            return {
                ...state,
                amazon_visible: !state.amazon_visible,
            }
        case actions.PRICE_HISTORY_NEW_VISIBLE:
            setDefaultValue('new_visible', !state.new_visible);
            return {
                ...state,
                new_visible: !state.new_visible,
            }
        case actions.PRICE_HISTORY_USED_ALL_VISIBLE:
            setDefaultValue('used_all_visible', !state.used_all_visible);
            return {
                ...state,
                used_all_visible: !state.used_all_visible,
                // used_like_new_visible: !state.used_all_visible,
                // used_very_good_visible: !state.used_all_visible,
                // used_good_visible: !state.used_all_visible,
                // used_acceptable_visible: !state.used_all_visible,
            }
        case actions.PRICE_HISTORY_USED_LIKE_NEW_VISIBLE:
            setDefaultValue('used_like_new_visible', !state.used_like_new_visible);
            return {
                ...state,
                used_like_new_visible: !state.used_like_new_visible,
                // used_all_visible: !state.used_like_new_visible && state.used_very_good_visible && state.used_good_visible && state.used_acceptable_visible,
            }
        case actions.PRICE_HISTORY_USED_VERY_GOOD_VISIBLE:
            setDefaultValue('used_very_good_visible', !state.used_very_good_visible);
            return {
                ...state,
                used_very_good_visible: !state.used_very_good_visible,
                // used_all_visible: state.used_like_new_visible && !state.used_very_good_visible && state.used_good_visible && state.used_acceptable_visible,
            }
        case actions.PRICE_HISTORY_USED_GOOD_VISIBLE:
            setDefaultValue('used_good_visible', !state.used_good_visible);
            return {
                ...state,
                used_good_visible: !state.used_good_visible,
                // used_all_visible: state.used_like_new_visible && state.used_very_good_visible && !state.used_good_visible && state.used_acceptable_visible,
            }
        case actions.PRICE_HISTORY_USED_ACCEPTABLE_VISIBLE:
            setDefaultValue('used_acceptable_visible', !state.used_acceptable_visible);
            return {
                ...state,
                used_acceptable_visible: !state.used_acceptable_visible,
                // used_all_visible: state.used_like_new_visible && state.used_very_good_visible && state.used_good_visible && !state.used_acceptable_visible,
            }
        case actions.PRICE_HISTORY_SALES_RANK_VISIBLE:
            setDefaultValue('salesRank_visible', !state.salesRank_visible);
            return {
                ...state,
                salesRank_visible: !state.salesRank_visible,
            }
        case actions.PRICE_HISTORY_SALES_RANK_FORECAST_VISIBLE:
            setDefaultValue('salesRank_forecast_visible', !state.salesRank_forecast_visible);
            return {
                ...state,
                salesRank_forecast_visible: !state.salesRank_forecast_visible,
            }
        case actions.PRICE_HISTORY_WAREHOUSE_VISIBLE:
            setDefaultValue('warehouse_visible', !state.warehouse_visible);
            return {
                ...state,
                warehouse_visible: !state.warehouse_visible
            }
        case actions.PRICE_HISTORY_AMAZON_RANGE:
            return {
                ...state,
                amazon: action.payload,
            }
        case actions.PRICE_HISTORY_NEW_RANGE:
            return {
                ...state,
                newone: action.payload
            }
        case actions.PRICE_HISTORY_USED_ALL_RANGE:
            return {
                ...state,
                used_all: action.payload,
            }
        case actions.PRICE_HISTORY_USED_LIKE_NEW_RANGE:
            return {
                ...state,
                used_like_new: action.payload
            }
        case actions.PRICE_HISTORY_USED_VERY_GOOD_RANGE:
            return {
                ...state,
                used_very_good: action.payload
            }
        case actions.PRICE_HISTORY_USED_GOOD_RANGE:
            return {
                ...state,
                used_good: action.payload
            }
        case actions.PRICE_HISTORY_USED_ACCEPTABLE_RANGE:
            return {
                ...state,
                used_acceptable: action.payload
            }
        case actions.PRICE_HISTORY_SALES_RANK_RANGE:
            return {
                ...state,
                salesRank: action.payload
            }
        case actions.PRICE_HISTORY_WAREHOUSE_RANGE:
            return {
                ...state,
                warehouse: action.payload
            }
        case actions.PRICE_HISTORY_SALES_RANK_REVERSED:
            setDefaultValue("salesRank_reversed", !state.salesRank_reversed);

            return {
                ...state,
                salesRank_reversed: !state.salesRank_reversed
            }
        case actions.PRICE_HISTORY_RANGE_DAY:
        case actions.PRICE_HISTORY_RANGE_WEEK:
        case actions.PRICE_HISTORY_RANGE_MONTH:
        case actions.PRICE_HISTORY_RANGE_3_MONTH:
        case actions.PRICE_HISTORY_RANGE_YEAR:
        case actions.PRICE_HISTORY_RANGE_ALL:
            setDefaultValue("range", action.type);
            return {
                ...state,
                range: action.type
            }
        default:
            return state;
    }
}

export default priceHistoryReducer;
export {
    actions
}