import React, { Component } from 'react';
import { connect } from 'react-redux';
import { actions } from './actions';
import Statistics from './components/statistics';
import MoreHistoricalData from './components/content/more_historical_data';
import Loading from './components/loading';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Col, Row } from 'react-bootstrap';
import Alert from './components/alert';
import ApiFetcher from './api_fetcher';
import BaAppBar from './components/ba_appbar';
import { convISBN13toISBN10 } from './convIsbn';
import {
  TextField,
  Checkbox,
  FormControlLabel,
  Button,
  Icon,
} from '@material-ui/core';

import amazonFbaIcon from './components/powered_by/assets/images/amazon-fba-icon.png';



class App extends Component {

  constructor() {
    super();

    this.state = {
      show_amazon_fba_link: false,
    }

    this.removeAlert = this.removeAlert.bind(this)
  }

  componentWillMount() {
    const qs = new URLSearchParams(window.location.search);

    if (!qs.has('r') || !/^[0-9\.]+$/.test(qs.get('r'))) {
      qs.set('r', Math.random());
      window.location.search = qs.toString();
      return;
    }

    if (!/^.*amazon\..*$/i.test(qs.get('s'))) {
      this.setState({
        ...this.state,
        show_amazon_fba_link: true
      });
    }

    let ISBN = window.location.hash.replace(/^#/, '');

    if (ISBN.length === 13) {
      ISBN = convISBN13toISBN10(ISBN);
    }

    if (!ISBN) {
      this.props.set_alert_title("ISBN of this book is not defined");
      this.props.set_alert_context_text("Refresh this page manually!");
      this.props.set_alert_open(true);
      this.props.change_progress_bar(false);
    } else {
      this.props.set_isbn(ISBN);
    }
  }

  removeAlert(e) {

    e.preventDefault();
    
    this.setState({
        ...this.state,
        saving_alert: true,
    });

    ApiFetcher.deleteTrackProduct(this.props.general.isbn)
        .then(response => {
            this.props.is_set_tracking(false);
            this.props.set_individual_price("");
            this.props.set_individual_conditions(this.props.settings.condition);
            this.props.set_individual_sites(this.props.settings.site);
            this.props.set_individual_description(this.props.settings.description);
            this.props.set_individual_seller_name(this.props.settings.seller_name);
            this.props.set_individual_seller_rating(this.props.settings.seller_rating);
            this.props.set_individual_seller_rating_percent(this.props.settings.seller_rating_percent);
            this.props.set_individual_shipping(this.props.settings.ships_from_us_only === true);
        })
        .catch(error => {
            this.props.set_alert_title("Track could not be deleted")
            this.props.set_alert_context_text(error && error.error_message);
            this.props.set_alert_open(true);
        }).finally(() => this.setState({
            ...this.state,
            saving_alert: false
        }))
  }

  componentDidMount() {
    setTimeout(() => {
      const xValueFormatString = "DD MMM YYYY hh:mm TT"

      this.props.change_progress_bar(true);

      let prev_timestamp = 0, diff = 0;
      ApiFetcher.getPriceHistory(this.props.general.isbn)
        .then(data => {
          if (!data.history || !(data.history instanceof Array) || !data.history.length) {
            this.props.set_alert_title("Price History Problem.")
            this.props.set_alert_context_text("Price history could not be loaded. May be ISBN number is wrong. Double check the ISBN and try again.");
            this.props.set_alert_open(true);
            return;
          }

          let amazon = [], news = [], used = [], sales_rank = [];

          this.props.set_statistics(data.statistics);
          let amazon_undefined_cnt = 0;

          data.history.sort((item1, item2) => item1.when > item2.when ? 1 : (item1.when < item2.when ? -1 : 0))

          data.history.forEach(value => {
            const x = new Date()
            x.setTime(value.when * 1000);

            // if (!value.value.amazon) {
            //   amazon_undefined_cnt++;
            // } else {
            //   amazon_undefined_cnt = 0;
            // }

            amazon.push({
              xValueFormatString,
              y: value.value.amazon || (amazon.length ? amazon[amazon.length - 1].y : 0),
              x
            });

            news.push({
              xValueFormatString,
              y: value.value.new || (news.length ? news[news.length - 1].y : 0),
              x
            });

            used.push({
              xValueFormatString,
              y: value.value.used || (used.length ? used[used.length - 1].y : 0),
              x
            });

            sales_rank.push({
              xValueFormatString,
              y: value.value.sales_rank || (sales_rank.length ? sales_rank[sales_rank.length - 1].y : 0),
              x
            });
          });

          this.props.set_amazon_range(amazon);
          this.props.set_used_all_range(used);
          this.props.set_new_range(news);
          this.props.set_sales_rank_range(sales_rank);
          this.props.is_set_tracking(data.tracking === true);
        })
        .catch(error => {
          // this.props.set_alert_title("Price History Problem")
          // this.props.set_alert_context_text(error.error_message);
          // this.props.set_alert_open(true);
        }).finally(() => this.props.change_progress_bar(false));
    }, 200);
  }
  render() {
    if (!this.props.general.isbn) {
      return (
        <div>
          <Alert />
          <Loading />
        </div>
      );
    }
    return (
      <div>
        <Alert />
        <Loading />
        <BaAppBar />
        <div style={{ minHeight: this.state.loading ? 80 : 450 }}>{this.props.content}</div>
        <Container>
          <Row>
            {/* <Col lg={2} md={1} xs={1}></Col> */}
            <Col lg={12} md={12} xs={12} className="text-left">
              {this.state.show_amazon_fba_link && (
                <div className="statistics-popup-container" style={{marginRight: '3em'}}>
                  <a href={`https://amazon.com/gp/offer-listing/${this.props.general.isbn}/ref=olp_f_new?ie=UTF8&f_used=true&f_usedLikeNew=true&f_usedVeryGood=true&f_usedGood=true&f_usedAcceptable=true&f_new=true&f_primeEligible=true`} target="_blank" className="statistics-link"><img src={amazonFbaIcon} style={{width: 20}} /> View Amazon Listings</a>
                </div>
              )}

              {this.props.track_product.isSetTracking && this.props.offers_history.offers_history_link_visible && (
                <div className="statistics-popup-container" style={{marginRight: '6em'}}>
                  <a href="" onClick={this.removeAlert} className="statistics-link"><Icon style={{ float: 'left' }}>delete</Icon> Delete Alert</a>
                </div>
              )}
              <Statistics />
              {this.props.offers_history.offers_history_link_visible ? <MoreHistoricalData /> : ""}
            </Col>
            {/* <Col lg={2} md={1} xs={1}></Col> */}
          </Row>
        </Container>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  content: state.content,
  offers_history: state.offers_history,
  progress_bar: state.progress_bar,
  general: state.general,
  alert: state.alert,
  track_product: state.track_product,
  settings: state.settings
})

const mapDispatchToProps = dispatch => {
  return {
    is_set_tracking: payload => dispatch({ type: actions.TRACK_PRODUCT_IS_SET, payload }),
    set_individual_isbn: payload => dispatch({ type: actions.TRACK_PRODUCT_ISBN, payload }),
    set_individual_price: payload => dispatch({ type: actions.TRACK_PRODUCT_PRICE, payload }),
    set_individual_conditions: payload => dispatch({ type: actions.TRACK_PRODUCT_CONDITION, payload }),
    set_individual_sites: payload => dispatch({ type: actions.TRACK_PRODUCT_SITE, payload }),
    set_individual_description: payload => dispatch({ type: actions.TRACK_PRODUCT_DESCRIPTION, payload }),
    set_individual_seller_name: payload => dispatch({ type: actions.TRACK_PRODUCT_SELLER_NAME, payload }),
    set_individual_seller_rating: payload => dispatch({ type: actions.TRACK_PRODUCT_SELLER_RATING, payload }),
    set_individual_seller_rating_percent: payload => dispatch({ type: actions.TRACK_PRODUCT_SELLER_RATING_PERCENT, payload }),
    set_individual_shipping: payload => dispatch({ type: actions.TRACK_PRODUCT_SHIP_US, payload }),

    show_price: action => dispatch({ type: action }),
    set_amazon_range: payload => dispatch({ type: actions.PRICE_HISTORY_AMAZON_RANGE, payload }),
    set_new_range: payload => dispatch({ type: actions.PRICE_HISTORY_NEW_RANGE, payload }),
    set_used_all_range: payload => dispatch({ type: actions.PRICE_HISTORY_USED_ALL_RANGE, payload }),
    set_used_like_new_range: payload => dispatch({ type: actions.PRICE_HISTORY_USED_LIKE_NEW_RANGE, payload }),
    set_used_very_good_range: payload => dispatch({ type: actions.PRICE_HISTORY_USED_VERY_GOOD_RANGE, payload }),
    set_used_good_range: payload => dispatch({ type: actions.PRICE_HISTORY_USED_GOOD_RANGE, payload }),
    set_used_acceptable_range: payload => dispatch({ type: actions.PRICE_HISTORY_USED_ACCEPTABLE_RANGE, payload }),
    set_sales_rank_range: payload => dispatch({ type: actions.PRICE_HISTORY_SALES_RANK_RANGE, payload }),
    set_isbn: payload => dispatch({ type: actions.ISBN_SET, payload }),
    set_statistics: payload => dispatch({ type: actions.STATISTICS_SET, payload }),
    change_progress_bar: payload => dispatch({ type: actions.PROGRESS_BAR_LOADING, payload }),
    set_alert_title: payload => dispatch({ type: actions.ALERT_TITLE, payload }),
    set_alert_context_text: payload => dispatch({ type: actions.ALERT_CONTEXT_TEXT, payload }),
    set_alert_open: payload => dispatch({ type: actions.ALERT_OPEN, payload }),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
