const actions = {
    STATISTICS_SET: "stc1"
}

let initialState = {
    statistics: null
}

const statisticsReducer = (state=initialState, action) => {
    switch(action.type) {
        case actions.STATISTICS_SET:
            return {
                ...state,
                statistics: action.payload
            }
        default:
            return state;
    }
}

export default statisticsReducer;
export {
    actions
}