import React, {Component} from 'react';
import {connect} from 'react-redux';
import {actions} from '../../../actions';
import data from './settings_offers_history_chart.json';
import ApiFetcher from '../../../api_fetcher';
import CanvasJSReact from '../../canvasjs/canvasjs.react';

var CanvasJSChart = CanvasJSReact.CanvasJSChart;

class OffersHistoryChart extends Component {
    constructor () {
        super()
        this.rangeChanged = this.rangeChanged.bind(this)
    }

    rangeChanged(e) {
        // callback came from property
        this.props.rangeChanged(e);
    }

    getAppropriateRange (items) {
        if (!items.length) {
            return items;
        }
        
        const min = new Date();

        switch (this.props.price_history.range) {
            case actions.PRICE_HISTORY_RANGE_DAY:
                min.setTime(min.getTime() - 60 * 60 * 24 * 1000);
                break;
            case actions.PRICE_HISTORY_RANGE_WEEK:
                min.setTime(min.getTime() - 60 * 60 * 24 * 8 * 1000);
                break;
            case actions.PRICE_HISTORY_RANGE_MONTH:
                min.setTime(min.getTime() - 60 * 60 * 24 * 32 * 1000);
                break;
            case actions.PRICE_HISTORY_RANGE_3_MONTH:
                min.setTime(min.getTime() - 60 * 60 * 24 * 31 * 3 * 1000);
                break;
            case actions.PRICE_HISTORY_RANGE_YEAR:
                min.setTime(min.getTime() - 60 * 60 * 24 * 366 * 1000);
                break;
            case actions.PRICE_HISTORY_RANGE_ALL:
                return items;
        }

        const filtered_items = [];

        for (let i = items.length - 1; i >= 0; i--) {
            if (items[i].x >= min) {
                let item = items[i];
                filtered_items.push(item);
            } else {
                break;
            }
        }

        return filtered_items;
    }

    componentWillMount () {
        if (!this.props.offers_history.used_offers.length && !this.props.offers_history.new_offers.length) {
            const used_offers = [], new_offers = [];
            const xValueFormatString = "DD MMM YYYY hh:mm TT";
            ApiFetcher.getPriceHistoryAdditional(this.props.general.isbn, ApiFetcher.ADDITIONAL_CATEGORY_HISTORICAL_DATA).then(data => {
                data.history.sort((item1, item2) => item1.when > item2.when ? 1 : (item1.when < item2.when ? -1 : 0));
                
                data.history.forEach(value => {
                    const x = new Date();
                    x.setTime(value.when * 1000);

                    new_offers.push({
                        xValueFormatString,
                        y: value.value.historical_data[0] && value.value.historical_data[0] !== -1 ? value.value.historical_data[0] : (new_offers.length ? new_offers[new_offers.length - 1].y : 0),
                        x
                    });

                    used_offers.push({
                        xValueFormatString,
                        y: value.value.historical_data[1] && value.value.historical_data[1] !== -1 ? value.value.historical_data[1] : (used_offers.length ? used_offers[used_offers.length - 1].y : 0),
                        x
                    });
                });



                this.props.set_new_offers(new_offers);
                this.props.set_used_offers(used_offers);

            }).catch(error => {
                // this.props.set_alert_title("More historical data problem")
                // this.props.set_alert_context_text(error.error_message);
                // this.props.set_alert_open(true);
            });
        }
    }

    render () {
        const PH = this.props.offers_history;

        data.data[0].dataPoints = this.getAppropriateRange(PH.used_offers || []);
        data.data[1].dataPoints = this.getAppropriateRange(PH.new_offers || []);

        switch(PH.range) {
            case actions.PRICE_HISTORY_RANGE_DAY:
                data.axisX.interval = 1;
                data.axisX.intervalType = 'hour';
                break;
            case actions.PRICE_HISTORY_RANGE_WEEK:
                data.axisX.interval = 1;
                data.axisX.intervalType = 'day';
                break;
            case actions.PRICE_HISTORY_RANGE_MONTH:
                data.axisX.interval = 2;
                data.axisX.intervalType = 'day';
                break;
            case actions.PRICE_HISTORY_RANGE_3_MONTH:
                data.axisX.interval = 1;
                data.axisX.intervalType = 'week';
                break;
            case actions.PRICE_HISTORY_RANGE_YEAR:
                data.axisX.interval = 1;
                data.axisX.intervalType = 'month';
                break;
            case actions.PRICE_HISTORY_RANGE_ALL:
                data.axisX.interval = 1;
                data.axisX.intervalType = 'year';
                break;
        }


        data.rangeChanged = this.rangeChanged;
        data.height = 250;
        
        if (data.data[0].dataPoints.length && data.data[1].dataPoints.length){
            return (<CanvasJSChart key={new Date().getTime() * Math.random()} options={data} />);
        } else {
            return "";
        }
    }
}

const mapStateToProps = state => ({
    price_history: state.price_history,
    offers_history: state.offers_history,
    general: state.general
})

const mapDispatchToProps = dispatch => {
    return {
        set_used_offers: payload => dispatch({type: actions.OFFERS_HISTORY_USED_RANGE, payload}),
        set_new_offers: payload => dispatch({type: actions.OFFERS_HISTORY_NEW_RANGE, payload}),
        set_alert_context_text: payload => dispatch({type: actions.ALERT_CONTEXT_TEXT, payload}),
        set_alert_open: payload => dispatch({type: actions.ALERT_OPEN, payload}),
        set_alert_title: payload => dispatch({type: actions.ALERT_TITLE, payload})
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(OffersHistoryChart);