import React, { Component } from 'react';
import { AppBar, Tabs, Tab, CircularProgress } from '@material-ui/core';
import { connect } from 'react-redux';
import {
    TextField,
    Checkbox,
    FormControlLabel,
    Button,
    Icon,
} from '@material-ui/core';
import { actions } from '../../actions';
import PoweredBy from '../powered_by';
import './assets/css/style.css';

const tabStyle = {
    fontSize: ".8em",
    minHeight: 'auto',
    minWidth: '50px',
}

const tabsStyle = {
    minHeight: 'auto'
}

class BaAppBar extends Component {
    constructor() {
        super();

        this.state = {
            activeIndex: actions.CONTENT_PRICE_HISTORY,
        }

        this.changeTab = this.changeTab.bind(this);
    }

    changeTab(e, index) {
        this.setState({
            ...this.state,
            activeIndex: index
        });

        this.props[index]();
    }

    componentDidMount() {
        this.props[this.state.activeIndex]();
    }
    render() {
        return (
            <AppBar position="static" color="default" className="BaAppBar">
                <Tabs value={this.state.activeIndex} onChange={this.changeTab} style={tabsStyle}>
                    <Tab value={actions.CONTENT_PRICE_HISTORY} style={tabStyle} label="Price history" />
                    <Tab value={actions.CONTENT_TRACK_PRODUCT} style={tabStyle} label={this.props.track_product.isSetTracking ?
                        <React.Fragment>
                            <span>UPDATE PRICE ALERT <Icon style={{ float: 'right' }}>notifications_none_alert</Icon></span>
                        </React.Fragment> :
                        <span>Add Price Alert</span>
                    } />
                    {/* <Tab value={actions.CONTENT_DATA} style={tabStyle} label="Data" /> */}
                    <Tab value={actions.CONTENT_SETTINGS} style={tabStyle} label="Alert Preset Rules" />
                </Tabs>
                <PoweredBy />
            </AppBar>
        )
    }
}

const mapStateToProps = state => ({
    // content: state.content,
    track_product: state.track_product
})

const mapDispatchToProps = dispatch => {
    let obj = {}
    obj[actions.CONTENT_PRICE_HISTORY] = () => {
        dispatch({ type: actions.CONTENT_PRICE_HISTORY });
        dispatch({ type: actions.OFFERS_HISTORY_LINK_VISIBLE, payload: true })
    }
    obj[actions.CONTENT_TRACK_PRODUCT] = () => {
        dispatch({ type: actions.CONTENT_TRACK_PRODUCT })
        dispatch({ type: actions.OFFERS_HISTORY_LINK_VISIBLE, payload: false })
    };
    obj[actions.CONTENT_DATA] = () => {
        dispatch({ type: actions.CONTENT_DATA })
        dispatch({ type: actions.OFFERS_HISTORY_LINK_VISIBLE, payload: false })
    };
    obj[actions.CONTENT_SETTINGS] = () => {
        dispatch({ type: actions.CONTENT_SETTINGS })
        dispatch({ type: actions.OFFERS_HISTORY_LINK_VISIBLE, payload: false })
    };

    return obj;
}

export default connect(mapStateToProps, mapDispatchToProps)(BaAppBar);