class SendPostMessage {

    static ifPossibleThenSend (func) {
        const obj = new SendPostMessage();
        if (window.parent && window.parent.postMessage) {
            func(obj);
        }
    }

    send (type, payload) {
        window.parent.postMessage({
            type,
            payload
        }, "*");
    }
}

export default SendPostMessage;