import { bool } from "prop-types"

const actions = {
    PROGRESS_BAR_LOADING: "prgb1"
}

const initialState = {
    loading: true
}

const progressBarReducer = (state=initialState, action) => {
    switch(action.type) {
        case actions.PROGRESS_BAR_LOADING:
            return {
                ...state,
                loading: !!action.payload
            }
        default:
            return state
    }
}

export default progressBarReducer;
export {
    actions
}