import contentReducer, {actions as content_actions} from './content';
import priceHistoryReducer, {actions as price_history_actions} from './price_history';
import offersHistoryReducer, {actions as offers_history_actions} from './offers_history';
import trackProductReducer, {actions as track_product_actions} from './track_product';
import statisticsReducer, {actions as statistics_actions} from './statistics';
import settingsReducer, {actions as settings_actions} from './settings';
import progressBarReducer, {actions as progress_bar_actions} from './progress_bar';
import generalReducer, {actions as general_actions} from './general';
import alertReducer, {actions as alert_actions} from './alert';
import {combineReducers} from 'redux';

const actions = {
    ...content_actions,
    ...price_history_actions,
    ...offers_history_actions,
    ...track_product_actions,
    ...statistics_actions,
    ...settings_actions,
    ...progress_bar_actions,
    ...general_actions,
    ...alert_actions
}

const reducers = combineReducers({
    content: contentReducer,
    price_history: priceHistoryReducer,
    offers_history: offersHistoryReducer,
    track_product: trackProductReducer,
    settings: settingsReducer,
    statistics: statisticsReducer,
    progress_bar: progressBarReducer,
    general: generalReducer,
    alert: alertReducer
});

export default reducers;
export {
    actions
}