import React, {Component} from 'react';
import {Icon} from '@material-ui/core';
import {actions} from '../../../actions';
import {connect} from 'react-redux';
import './assets/css/style.css';

class MoreHistoricalData extends Component {
    constructor () {
        super();
    }

    render () {
        return <div className="more-historical-data-container">
            <a href="" onClick={e => {
                e.preventDefault();
                this.props.set_offers_history_visibility();
            }} className={`more-historical-data-link ${this.props.offers_history_visible ? 'active': ''}`}>{this.props.offers_history_visible ? !this.props.offers_history.new_offers.length || !this.props.offers_history.used_offers.length ? <Icon style={{float: 'left'}}>hourglass_empty</Icon> : <Icon>minimize</Icon> : <Icon style={{float: 'left'}}>history</Icon>}
            {this.props.offers_history_visible ? "Less Historical Data" : "More Historical Data"}</a>
        </div>
    }
}

const mapStateToProps = state => {
    return {
        offers_history_visible: state.offers_history.offers_history_visible,
        offers_history: state.offers_history,
        general: state.general
    }
}

const mapDispatchToProps = dispatch => {
    return {
        set_offers_history_visibility: () => dispatch({type: actions.OFFERS_HISTORY_VISIBLE}),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(MoreHistoricalData);