const actions = {
    // SETTINGS_ISBN: "SETTINGS_ISBN",
    // SETTINGS_PRICE: "SETTINGS_PRICE",
    SETTINGS_CONDITION: "stn1",
    SETTINGS_DESCRIPTION: "stn2",
    SETTINGS_SELLER_NAME: "stn3",
    SETTINGS_SELLER_RATING: "stn4",
    SETTINGS_SELLER_RATING_PERCENT: "stn5",
    SETTINGS_SHIP_US: "stn6",
    SETTINGS_EMAIL: "stn7",
    SETTINGS_IS_SET: "stn8",
    SETTINGS_SITE: "stn9",
}

let initialState = {
    isSetSettings: false,
    condition: [],
    site: [],
    description: [],
    seller_name: [],
    seller_rating: "",
    seller_rating_percent: "",
    ships_from_us_only: false,
    notification_email: "",
}

const settingsReducer = (state = initialState, action) => {
    switch (action.type) {
        // case actions.SETTINGS_PRICE:
        //     return {
        //         ...state,
        //         price: action.payload
        //     }
        case actions.SETTINGS_CONDITION:
            return {
                ...state,
                condition: action.payload
            }
        case actions.SETTINGS_DESCRIPTION:
            return {
                ...state,
                description: action.payload
            }
        case actions.SETTINGS_SELLER_NAME:
            return {
                ...state,
                seller_name: action.payload
            }
        case actions.SETTINGS_SELLER_RATING:
            return {
                ...state,
                seller_rating: action.payload
            }
        case actions.SETTINGS_SELLER_RATING_PERCENT:
            return {
                ...state,
                seller_rating_percent: action.payload
            }
        case actions.SETTINGS_SHIP_US:
            return {
                ...state,
                ships_from_us_only: action.payload
            }
        case actions.SETTINGS_EMAIL:
            return {
                ...state,
                notification_email: action.payload
            }
        case actions.SETTINGS_IS_SET:
            return {
                ...state,
                isSetSettings: action.payload
            }
        case actions.SETTINGS_SITE:
            return {
                ...state,
                site: action.payload
            }
        default:
            return state;
    }
}

export default settingsReducer;
export {
    actions
}