import React, { Component } from 'react';
import { connect } from 'react-redux';
import { actions } from '../../../actions';
import data from './settings_price_history_chart.json';
import CanvasJSReact from '../../canvasjs/canvasjs.react';

data.axisY[1].labelFormatter = () => '';

var CanvasJS = CanvasJSReact.CanvasJS;
var CanvasJSChart = CanvasJSReact.CanvasJSChart;

const MONTH = [
    "Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"
];

class PriceHistoryChart extends Component {
    constructor() {
        super();

        this.rangeChanged = this.rangeChanged.bind(this)
    }

    componentDidUpdate() {
        this.chart.render();
    }

    rangeChanged(e) {
        // callback came from property
        this.props.rangeChanged(e);
    }

    getSalesRankForecastData (items, amazon) {
        const now = new Date(items[items.length - 1] && items[items.length - 1].x.getTime());
        const start = new Date();
        const end = new Date();

        start.setYear(now.getFullYear() - 1);
        start.setMonth(now.getMonth());
        start.setDate(now.getDate());
        end.setYear(new Date().getFullYear() - 1);
        end.setMonth(new Date().getMonth() + 2);

        let forecast = items.filter(obj => obj.x >= start && obj.x <= end);

        forecast = forecast.map(obj => {
            const x = new Date(obj.x.getTime());
            x.setYear(x.getFullYear() + 1);

            const hours = x.getHours() % 12;

            obj = {
                x,
                y: obj.y,
                xValueFormatString: obj.xValueFormatString,
            }
            
            return obj;
        });

        if (forecast[0]) {
            forecast[0].y = items[items.length - 1].y;
        }

        return forecast;
    }

    fillAmazonGaps(items, i) {
        const item = items[i];
        if (item === undefined) {
            return items[i];
        }

        if (item.y < 0) {
            item.y = null;
        }

        return;

        const x = new Date(item.x.getTime());
        const hours = x.getHours() % 12;
        item.x = x;
        item.toolTipContent = `${x.getDate()} ${MONTH[x.getMonth()]} ${x.getFullYear()} ${(hours > 0 && hours < 10) ? '0' + hours : (hours ? hours : 12)}:${x.getMinutes()} ${x.getHours() >= 12 ? 'PM' : 'AM'}<br>
                <span style="color:rgb(255, 165, 0)">Amazon:</span> <span>${item.y}</span>`;

        
        let scale = 1;

        switch(this.props.price_history.range) {
            case actions.PRICE_HISTORY_RANGE_3_MONTH:
                scale = 1.1;
                break;
            case actions.PRICE_HISTORY_RANGE_YEAR:
                scale = 1.1;
                break;
            case actions.PRICE_HISTORY_RANGE_ALL:
                scale = null;
                break;
        }

        let lastNonZeroValue = 0;
        for(let e = i; e > 0; e--) {
            if (items[e].y > 0) {
                lastNonZeroValue = items[e].y;
                break;
            }
        }

        if (item.y < 0) {
            item.y = scale !== null && (lastNonZeroValue && lastNonZeroValue / scale) || null;
            item.isGap = true;
        }
    }

    getAppropriateRange(items, history_type = null) {
        if (!items.length) {
            return items;
        }

        const min = new Date();
        let warehouseIncreasingStep = 0;

        switch (this.props.price_history.range) {
            case actions.PRICE_HISTORY_RANGE_DAY:
                min.setTime(min.getTime() - 60 * 60 * 24 * 1000);
                // hourly
                warehouseIncreasingStep = 3600000;
                break;
            case actions.PRICE_HISTORY_RANGE_WEEK:
                min.setTime(min.getTime() - 60 * 60 * 24 * 8 * 1000);
                // 4 hours
                warehouseIncreasingStep = 14400000;
                break;
            case actions.PRICE_HISTORY_RANGE_MONTH:
                min.setTime(min.getTime() - 60 * 60 * 24 * 32 * 1000);
                // 8 hours
                warehouseIncreasingStep = 28800000;
                break;
            case actions.PRICE_HISTORY_RANGE_3_MONTH:
                min.setTime(min.getTime() - 60 * 60 * 24 * 31 * 3 * 1000);
                // daily
                warehouseIncreasingStep = 86400000;
                break;
            case actions.PRICE_HISTORY_RANGE_YEAR:
                min.setTime(min.getTime() - 60 * 60 * 24 * 366 * 1000);
                // daily
                warehouseIncreasingStep = 86400000;
                break;
            case actions.PRICE_HISTORY_RANGE_ALL:
                // daily
                warehouseIncreasingStep = 86400000;
                if (history_type == actions.PRICE_HISTORY_AMAZON_RANGE) {
                    for (let i = items.length - 1; i >= 0; i--) {
                        this.fillAmazonGaps(items, i);
                    }
                }
                return items;
        }

        let filtered_items = [];
        const LIMIT_MINIMAL = 320;

        // if (history_type === actions.PRICE_HISTORY_WAREHOUSE_RANGE) {
        //     console.log("FFFFF", items.length);
        // }

        for (let i = items.length - 1; i >= 0; i--) {
            if (items[i].x >= min) {
                let item = items[i];

                const x = new Date(item.x.getTime());
                x.setYear(x.getFullYear() + 1);              

                if (history_type == actions.PRICE_HISTORY_AMAZON_RANGE) {
                    this.fillAmazonGaps(items, i);
                }
                
                filtered_items.push(item);

                // burada ozunden evvelki vaxtla indikini muqayise edirik,
                // eger ferq warehouseIncreasingStep-den boyukdurse onda aradaki boshlugu
                // bir evvelki deyerle doldururuq saat-ba-saat
                if (history_type === actions.PRICE_HISTORY_WAREHOUSE_RANGE && items.length > LIMIT_MINIMAL) {
                    const prevItem = filtered_items[filtered_items.length - 2];
                    if (!prevItem) {
                        continue;
                    }

                    const diff = prevItem.x.getTime() - item.x.getTime();
                    if (diff > warehouseIncreasingStep) {
                        for (let i = prevItem.x.getTime(); i > item.x.getTime(); i -= warehouseIncreasingStep) {
                            filtered_items.push({
                                xValueFormatString: item.xValueFormatString,
                                x: new Date(i),
                                y: item.y
                            });
                        }
                    }
                }
            } else {
                break;
            }
        }

        if (history_type === actions.PRICE_HISTORY_WAREHOUSE_RANGE && items.length > LIMIT_MINIMAL && filtered_items.length > 1) {
            // warehouse chartin evvelinde bosh qalan yeri doldururuq
            // en kohne warehouse tapiriq, lakin yoxlamaq lazimdir ki, o canvasjs-in viewportminimumundan kohne olmasin.
            let firstItem = filtered_items[filtered_items.length - 1];

            for (let i = firstItem.x.getTime(); i >= this.chart.axisX[0].viewportMinimum; i -= warehouseIncreasingStep) {
                filtered_items.push({
                    xValueFormatString: firstItem.xValueFormatString,
                    x: new Date(i),
                    y: firstItem.y
                });
            }

            let lastItem = filtered_items[0];
            for (let i = this.chart.axisX[0].viewportMaximum; i >= lastItem.x.getTime(); i -= warehouseIncreasingStep) {
                filtered_items.push({
                    xValueFormatString: lastItem.xValueFormatString,
                    x: new Date(i),
                    y: lastItem.y
                });
            }
        }

        // console.log("ZZZ", items[0])

        // eger warehouse item bir ve ya sifirdirsa (boyuk boshluq sebebinden)
        // onda onu en son warehouse melumatiyla doldururuq
        // if (history_type === actions.PRICE_HISTORY_WAREHOUSE_RANGE && filtered_items.length <= 1 && items.length > 200) {
        // let lastItem = items[items.length - 1];
        // for (let i = Date.now(); i >= lastItem.x.getTime(); i -= warehouseIncreasingStep) {
        //     filtered_items.push({
        //         xValueFormatString: lastItem.xValueFormatString,
        //         x: new Date(i),
        //         y: lastItem.y
        //     });
        // }
        // }


        return filtered_items;
    }

    render() {
        const PH = this.props.price_history;

        data.axisY2.reversed = PH.salesRank_reversed;

        data.data[0].dataPoints = this.getAppropriateRange(PH.amazon_visible ? PH.amazon : [], actions.PRICE_HISTORY_AMAZON_RANGE);
        data.data[0].visible = PH.amazon_visible;
        //data.data[0].lineThickness = PH.amazon_line_thickness;

        data.data[1].dataPoints = this.getAppropriateRange(PH.new_visible ? PH.newone : [], actions.PRICE_HISTORY_NEW_RANGE);
        data.data[1].visible = PH.new_visible;
        //data.data[1].lineThickness = PH.new_line_thickness;

        data.data[2].dataPoints = this.getAppropriateRange(PH.used_all_visible ? PH.used_all : [], actions.PRICE_HISTORY_USED_ALL_RANGE);
        data.data[2].visible = PH.used_all_visible;
        //data.data[2].lineThickness = PH.used_line_thickness;

        data.data[3].dataPoints = this.getAppropriateRange(PH.used_like_new_visible ? PH.used_like_new : [], actions.PRICE_HISTORY_USED_LIKE_NEW_RANGE);
        data.data[3].visible = PH.used_like_new_visible;
        //data.data[3].markerSize = PH.used_like_new_line_thickness;

        data.data[4].dataPoints = this.getAppropriateRange(PH.used_very_good_visible ? PH.used_very_good : [], actions.PRICE_HISTORY_USED_VERY_GOOD_RANGE);
        data.data[4].visible = PH.used_very_good_visible;
        //data.data[4].markerSize = PH.used_very_good_line_thickness;

        data.data[5].dataPoints = this.getAppropriateRange(PH.used_good_visible ? PH.used_good : [], actions.PRICE_HISTORY_USED_GOOD_RANGE);
        data.data[5].visible = PH.used_good_visible;
        //data.data[5].markerSize = PH.used_good_line_thickness;

        data.data[6].dataPoints = this.getAppropriateRange(PH.used_acceptable_visible ? PH.used_acceptable : [], actions.PRICE_HISTORY_USED_ACCEPTABLE_RANGE);
        data.data[6].visible = PH.used_acceptable_visible;
        //data.data[6].markerSize = PH.used_acceptable_line_thickness;

        data.data[7].dataPoints = this.getAppropriateRange(PH.salesRank_visible ? PH.salesRank : [], actions.PRICE_HISTORY_SALES_RANK_RANGE);
        data.data[7].visible = PH.salesRank_visible;
        //data.data[7].lineThickness = PH.sales_rank_line_thickness;

        data.data[8].dataPoints = this.getAppropriateRange(PH.warehouse_visible ? PH.warehouse : [], actions.PRICE_HISTORY_WAREHOUSE_RANGE);
        data.data[8].visible = PH.warehouse_visible;

        data.data[9].dataPoints = this.getSalesRankForecastData(PH.salesRank, PH.amazon);
        data.data[9].visible = PH.salesRank_forecast_visible;
        data.axisY[1].maximum = Math.max(...PH.salesRank.map(item => item.y));

        switch (PH.range) {
            case actions.PRICE_HISTORY_RANGE_DAY:
                if (data.data[9].dataPoints && PH.salesRank_forecast_visible) {
                    data.axisX.interval = 4;
                    data.axisX.intervalType = 'day';
                } else {
                    data.axisX.interval = 1;
                    data.axisX.intervalType = 'hour';
                }
                break;
            case actions.PRICE_HISTORY_RANGE_WEEK:
                if (data.data[9].dataPoints && PH.salesRank_forecast_visible) {
                    data.axisX.interval = 4;
                    data.axisX.intervalType = 'day';
                } else {
                    data.axisX.interval = 1;
                    data.axisX.intervalType = 'day';
                }
                break;
            case actions.PRICE_HISTORY_RANGE_MONTH:
                if (data.data[9].dataPoints && PH.salesRank_forecast_visible) {
                    data.axisX.interval = 4;
                    data.axisX.intervalType = 'day';
                } else {
                    data.axisX.interval = 2;
                    data.axisX.intervalType = 'day';
                }
                break;
            case actions.PRICE_HISTORY_RANGE_3_MONTH:
                data.axisX.interval = 1;
                data.axisX.intervalType = 'week';
                break;
            case actions.PRICE_HISTORY_RANGE_YEAR:
                data.axisX.interval = 1;
                data.axisX.intervalType = 'month';
                break;
            case actions.PRICE_HISTORY_RANGE_ALL:
                data.axisX.interval = 5;
                data.axisX.intervalType = 'month';
                break;
        }

        // if (this.props.price_history.resetZooming) {
        //     data.axisX.minimum = null;
        //     data.axisX.maximum = null;
        // }


        data.rangeChanged = this.rangeChanged;
        data.height = 450;

        return (<CanvasJSChart onRef={ref => {
            this.chart = ref;
            if (this.props.onRef) {
                this.props.onRef(ref);
            }
        }} options={data} />);
    }
}

const mapStateToProps = state => ({
    price_history: state.price_history,
})

const mapDispatchToProps = dispatch => {
    return {

    }
}

export default connect(mapStateToProps, mapDispatchToProps)(PriceHistoryChart);