import React, { Component } from 'react';
import { Container, Col, Row } from 'react-bootstrap';
import { TextField, Checkbox, FormControlLabel, Button, Icon } from '@material-ui/core';
import ChipInput from 'material-ui-chip-input';
import { connect } from 'react-redux';
import './css/style.css';
import { actions } from '../../../actions';
import ApiFetcher from '../../../api_fetcher';
import TrackButton from '../track_product/TrackButton';
import CustomTooltip from '../../custom_tooltip';
import amzIcon from '../images/amz-logo-small2.png';
import abeIcon from '../images/abe-logo.png';
import bibIcon from '../images/bib-logo.png';
import tbrIcon from '../images/tbr-logo.png';
import albIcon from '../images/alb-logo.png';

class Settings extends Component {
    constructor() {
        super()

        this.state = {
            saving_alert: false,
            tracking_limit_error_text: "",
            tracking_limit_error: false,
            site_checking_error: false,
        }

        this.inputChanged = this.inputChanged.bind(this)
        this.conditionChecked = this.conditionChecked.bind(this)
        this.siteChecked = this.siteChecked.bind(this)
        this.shippingChecked = this.shippingChecked.bind(this)
        this.saveSettings = this.saveSettings.bind(this)
    }

    componentWillMount() {
        if (!this.props.track_product.isbn) {
            this.props.set_progress_bar_loading(true);
            ApiFetcher.getTrackProduct(this.props.general.isbn).then(data => {
                let { individual, global } = data;

                individual = individual || {};
                global = global || {};

                const conditions = {
                    "1": "new",
                    "2": "used_like_new",
                    "3": "used_very_good",
                    "4": "used_good",
                    "5": "used_acceptable"
                }

                let cnd = [];
                let isTrackingSet = !!individual.price && individual.price != '0.00';
                if (individual.conditions && individual.conditions.length) {
                    cnd = individual.conditions.map(key => conditions[key]);
                } else if (!isTrackingSet && global.conditions && global.conditions.length) {
                    cnd = global.conditions.map(key => conditions[key]);
                }

                this.props.is_set_tracking(isTrackingSet);
                this.props.set_individual_isbn(individual.isbn || "");
                this.props.set_individual_conditions(cnd);
                this.props.set_individual_sites(individual.sites || []);
                this.props.set_individual_description(individual.description || (!isTrackingSet && global.description) || []);
                this.props.set_individual_price(individual.price || "");
                this.props.set_individual_seller_name(individual.seller_name || (!isTrackingSet && global.seller_name) || []);
                this.props.set_individual_seller_rating(individual.seller_rating || (!isTrackingSet && global.seller_rating) || "");
                this.props.set_individual_seller_rating_percent(individual.seller_rating_percent || (!isTrackingSet && global.seller_rating_percent) || "");
                this.props.set_individual_shipping(individual.ships_from_us_only || (!isTrackingSet && global.ships_from_us_only) || "");

                this.props.is_set_settings(!!global.notification_email);
                this.props.set_global_conditions(global.conditions && global.conditions.length ? global.conditions.map(key => conditions[key]) : []);
                this.props.set_global_sites(global.sites || []);
                this.props.set_global_description(global.description || []);
                this.props.set_global_seller_name(global.seller_name || []);
                this.props.set_global_seller_rating(global.seller_rating || "");
                this.props.set_global_seller_rating_percent(global.seller_rating_percent || "");
                this.props.set_global_shipping(global.ships_from_us_only || "");
                this.props.set_global_email(global.notification_email || "");

                setTimeout(() => this.props.set_progress_bar_loading(false), 1000);
            }).catch(error => {
                this.props.set_alert_title("Settings could not be loaded")
                this.props.set_alert_context_text(error.error_message)
                this.props.set_alert_open(true)
            });
        }
    }

    setGlobalSettings() {

        if (!this.props.track_product.seller_rating && this.props.settings.seller_rating) {
            this.props.set_individual_seller_rating(this.props.settings.seller_rating)
        }

        if (!this.props.track_product.seller_rating_percent && this.props.settings.seller_rating_percent) {
            this.props.set_individual_seller_rating_percent(this.props.settings.seller_rating_percent)
        }

        if (!this.props.track_product.condition.length && this.props.settings.condition.length) {
            this.props.set_individual_conditions(this.props.settings.condition.slice(0))
        }

        // if (!this.props.track_product.site.length && this.props.settings.site.length) {
        //     this.props.set_individual_sites(this.props.settings.site.slice(0))
        // }

        if (!this.props.track_product.description.length && this.props.settings.description.length) {
            this.props.set_individual_description(this.props.settings.description.slice(0))
        }

        if (!this.props.track_product.seller_name.length && this.props.settings.seller_name.length) {
            this.props.set_individual_seller_name(this.props.settings.seller_name.slice(0))
        }

        if (!this.props.track_product.ships_from_us_only && this.props.settings.ships_from_us_only) {
            this.props.set_individual_shipping(this.props.settings.ships_from_us_only)
        }
    }

    componentDidMount() {

    }

    saveSettings(e) {
        if (!this.props.settings.notification_email) {
            this.props.set_alert_title('Warning!');
            this.props.set_alert_context_text('To save settings you have to set your email address');
            this.props.set_alert_open(true);
            return;
        }

        if (!this.props.settings.site.length) {
            this.props.set_alert_title('Warning!');
            this.props.set_alert_context_text('At least one site should be checked');
            this.props.set_alert_open(true);
            return;
        }


        const conditions = {
            new: 1,
            used_like_new: 2,
            used_very_good: 3,
            used_good: 4,
            used_acceptable: 5
        }

        const data = {
            section: 2,
            condition: this.props.settings.condition.map(key => conditions[key]),
            description: this.props.settings.description,
            seller_name: this.props.settings.seller_name,
            seller_rating: this.props.settings.seller_rating,
            seller_rating_percent: this.props.settings.seller_rating_percent,
            ships_from_us_only: this.props.settings.ships_from_us_only ? 1 : "",
            notification_email: this.props.settings.notification_email,
            site: this.props.settings.site || []
        }

        this.setState({
            ...this.state,
            saving_alert: true
        });

        ApiFetcher.updateTrackProduct(data, this.props.track_product.isbn)
            .then(response => {
                this.props.is_set_settings(true);

                if (response.alert_limit && response.alert_count && parseInt(response.alert_count) > parseInt(response.alert_limit)) {
                    this.setState({
                        ...this.state,
                        tracking_limit_error: true,
                        tracking_limit_error_text: `You are using ${response.alert_count}/${response.alert_limit} alerts. Please upgrade your alert tracking limit by going to <a href="https://alerts.booktrades.biz/app/alert3/index" target="_blank">membercentral.net</a>`
                    });

                    this.props.set_alert_title('Warning!');
                    this.props.set_alert_context_text(`Your alert was saved, however, it will not be tracked until you upgrade your alert package because this exceeds your maximum number of alerts allowed. You are currently using ${response.alert_count}/${response.alert_limit} alerts.`);
                    this.props.set_alert_open(true);
                }
            })
            .catch(error => {
                this.props.set_alert_title("Settings could not be saved")
                this.props.set_alert_context_text(error.error_message)
                this.props.set_alert_open(true)
            })
            .finally(() => this.setState({
                ...this.state,
                saving_alert: false
            }));
    }

    shippingChecked(e) {
        const settings = this.props.settings;
        this.props.set_global_shipping(!settings.ships_from_us_only)
    }

    siteChecked(e) {
        const target = e.target;
        const value = target.value;
        const settings = this.props.settings
        const index = settings.site.indexOf(value);
        if (index === -1) {
            settings.site.push(value);
        } else {
            settings.site.splice(index, 1);
        }

        this.props.set_global_sites(settings.site);
    }

    conditionChecked(e) {
        const target = e.target;
        const value = target.value;
        const settings = this.props.settings
        const index = settings.condition.indexOf(value);
        if (index === -1) {
            settings.condition.push(value);
        } else {
            settings.condition.splice(index, 1);
        }

        this.props.set_global_conditions(settings.condition);
    }

    inputChanged(e) {
        const target = e.target;
        switch (target.id) {
            case actions.SETTINGS_ISBN:
                this.props.set_global_isbn(target.value);
                break;
            case actions.SETTINGS_PRICE:
                this.props.set_global_price(target.value);
                break;
            case actions.SETTINGS_SELLER_RATING:
                this.props.set_global_seller_rating(target.value);
                break;
            case actions.SETTINGS_SELLER_RATING_PERCENT:
                this.props.set_global_seller_rating_percent(target.value);
                break;
            case actions.SETTINGS_EMAIL:
                this.props.set_global_email(target.value);
                break;
        }
    }
    render() {
        if (!this.props.track_product.isbn) {
            this.props.set_individual_isbn(this.props.general.isbn);
            setTimeout(() => this.setGlobalSettings(), 200);
        }

        const settings = this.props.settings;

        // console.log("ZZZ", this.props.track_product.sites, this.props.settings.sites);

        return (
            <Container id="track-product" fluid={true}>
                <Row>
                    <Col xs={12}>
                        <h2 className="text-center">ALERT PRESET RULES
                        <CustomTooltip>Use the fields below to set values that will be automatically applied to <strong>EVERY</strong> individual book alert that you set.  If there are common settings that you want every alert to use, this is the place to do it so you do not have to keep entering the same settings over and over.  Also, you can override these settings on any individual alert, so you are not stuck with them if you want to change a specific alert but keep them for the others.  Please be sure to click the Save Global Settings blue button once done.</CustomTooltip>
                        </h2>
                        {this.state.tracking_limit_error && <div className="alert alert-warning text-center" dangerouslySetInnerHTML={{ __html: this.state.tracking_limit_error_text }} />}
                        <hr />
                    </Col>
                </Row>
                <Row>
                    <Col lg={3} md={3} xs={3}>
                        <TextField
                            id={actions.SETTINGS_SELLER_RATING}
                            value={this.props.settings.seller_rating}
                            label="Seller rating - Min. # Feedback"
                            margin="normal"
                            variant="standard"
                            type="number"
                            onChange={this.inputChanged}
                            InputProps={{
                                endAdornment: <CustomTooltip>Please enter the least number of feedback ratings that you would want any seller on all alerts to have.  For example, the seller must have at least 20 ratings, etc.</CustomTooltip>
                            }}
                            error={this.state.tracking_limit_error}
                        />
                        <TextField
                            id={actions.SETTINGS_SELLER_RATING_PERCENT}
                            value={this.props.settings.seller_rating_percent}
                            label="Seller Rating - Min. %"
                            margin="normal"
                            variant="standard"
                            type="number"
                            onChange={this.inputChanged}
                            InputProps={{
                                endAdornment: <CustomTooltip>Please enter the lowest rating % you would want any seller of all your alerts to have.  For example, the seller must have at least a 90% positive star rating.</CustomTooltip>
                            }}
                            error={this.state.tracking_limit_error}
                        />
                    </Col>
                    <Col lg={3} md={3} xs={3}>
                        <div className="text-center">Sites:
                            <CustomTooltip>Please select the conditions of the book that you would want all alerts to default to be alerted on.  You can pick as many as you like, however, if you do not pick at least one, then then this setting is ignored.</CustomTooltip>
                        </div>
                        <Row>
                            <Col lg={12} md={12} xs={12}>
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            color="primary"
                                            checked={settings.site.indexOf("AMZ") !== -1}
                                            onChange={this.siteChecked}
                                            value="AMZ"
                                            error={this.state.tracking_limit_error || this.state.site_checking_error}
                                        />
                                    }
                                    label={<img src={amzIcon} style={{ width: 60 }} />}
                                />
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            color="primary"
                                            checked={settings.site.indexOf("ABE") !== -1}
                                            onChange={this.siteChecked}
                                            value="ABE"
                                            error={this.state.tracking_limit_error || this.state.site_checking_error}
                                        />
                                    }
                                    label={<img src={abeIcon} style={{ width: 60 }} />}
                                />
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            color="primary"
                                            checked={settings.site.indexOf("ALB") !== -1}
                                            onChange={this.siteChecked}
                                            value="ALB"
                                            error={this.state.tracking_limit_error || this.state.site_checking_error}
                                        />
                                    }
                                    label={<img src={albIcon} style={{ width: 60 }} />}
                                />
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            color="primary"
                                            checked={settings.site.indexOf("BIB") !== -1}
                                            onChange={this.siteChecked}
                                            value="BIB"
                                            error={this.state.tracking_limit_error || this.state.site_checking_error}
                                        />
                                    }
                                    label={<img src={bibIcon} style={{ width: 60 }} />}
                                />
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            color="primary"
                                            checked={settings.site.indexOf("TBR") !== -1}
                                            onChange={this.siteChecked}
                                            value="TBR"
                                            error={this.state.tracking_limit_error || this.state.site_checking_error}
                                        />
                                    }
                                    label={<img src={tbrIcon} style={{ width: 60 }} />}
                                />
                            </Col>
                        </Row>
                    </Col>
                    <Col lg={3} md={3} xs={3}>
                        <div className="text-center">Condition:
                            <CustomTooltip>Please select the conditions of the book that you would want all alerts to default to be alerted on.  You can pick as many as you like, however, if you do not pick at least one, then then this setting is ignored.</CustomTooltip>
                        </div>
                        <Row>
                            <Col lg={12} md={12} xs={12}>
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            color="primary"
                                            checked={settings.condition.indexOf("new") !== -1}
                                            onChange={this.conditionChecked}
                                            value="new"
                                            error={this.state.tracking_limit_error}
                                        />
                                    }
                                    label="New"
                                />
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            color="primary"
                                            checked={settings.condition.indexOf("used_like_new") !== -1}
                                            onChange={this.conditionChecked}
                                            value="used_like_new"
                                            error={this.state.tracking_limit_error}
                                        />
                                    }
                                    label="Used - Like New"
                                />
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            color="primary"
                                            checked={settings.condition.indexOf("used_very_good") !== -1}
                                            onChange={this.conditionChecked}
                                            value="used_very_good"
                                            error={this.state.tracking_limit_error}
                                        />
                                    }
                                    label="Used - Very Good"
                                />
                                <FormControlLabel
                                    className="form-control-label"
                                    control={
                                        <Checkbox
                                            color="primary"
                                            checked={settings.condition.indexOf("used_good") !== -1}
                                            onChange={this.conditionChecked}
                                            value="used_good"
                                            error={this.state.tracking_limit_error}
                                        />
                                    }
                                    label="Used - Good"
                                />
                                <FormControlLabel
                                    className="form-control-label"
                                    control={
                                        <Checkbox
                                            color="primary"
                                            checked={settings.condition.indexOf("used_acceptable") !== -1}
                                            onChange={this.conditionChecked}
                                            value="used_acceptable"
                                            error={this.state.tracking_limit_error}
                                        />
                                    }
                                    label="Used - Acceptable"
                                />
                            </Col>
                        </Row>
                    </Col>
                    <Col lg={3} md={3} xs={3}>
                        <FormControlLabel
                            control={
                                <ChipInput
                                    // style={{maxHeight: "90px", overflow: "auto"}}
                                    value={this.props.settings.description}
                                    onAdd={chip => {
                                        const chips = this.props.settings.description;
                                        chips.push(chip);
                                        this.props.set_global_description(chips);
                                    }}
                                    onDelete={(chip, index) => {
                                        const chips = this.props.settings.description;
                                        chips.splice(chips.indexOf(chip), 1);
                                        this.props.set_global_description(chips);
                                    }}
                                    InputProps={{
                                        endAdornment: <CustomTooltip>Please enter words or phrases that you do not want any book listing to have in its description.  You can enter as many as you like, but press the enter key after each word or phrase that you want to exclude.  For example, water damage.</CustomTooltip>
                                    }}
                                    error={this.state.tracking_limit_error}
                                />
                            }
                            label="Description (Does NOT Contain)"
                        />
                        <FormControlLabel
                            control={
                                <ChipInput
                                    // style={{maxHeight: "90px", overflow: "auto"}}
                                    value={this.props.settings.seller_name}
                                    onAdd={chip => {
                                        const chips = this.props.settings.seller_name;
                                        chips.push(chip);
                                        this.props.set_global_seller_name(chips);
                                    }}
                                    onDelete={(chip, index) => {
                                        const chips = this.props.settings.seller_name;
                                        chips.splice(chips.indexOf(chip), 1);
                                        this.props.set_global_seller_name(chips);
                                    }}
                                    InputProps={{
                                        endAdornment: <CustomTooltip>Please enter the specific names of sellers that you want to exclude from being tracked on all alerts.  You can enter multiple names to exclude, just be sure to hit the enter key after each one you type.</CustomTooltip>
                                    }}
                                    error={this.state.tracking_limit_error}
                                />
                            }
                            label="Seller name (Is NOT)"
                        />
                        <FormControlLabel
                            control={
                                <Checkbox
                                    color="primary"
                                    checked={!!settings.ships_from_us_only}
                                    onChange={this.shippingChecked}
                                    error={this.state.tracking_limit_error}
                                />
                            }
                            label={
                                <span>
                                    Shipping Location - Ships from US only (if known)
                                    <CustomTooltip>Check this box if you want to only track book listings for all alerts that we can identify as being shipped from the United States.  If we cannot determine its shipping location, we will include it as being tracked.  However, if this is checked and we can determine the book listing is being shipped from outside the United States, then we will exclude it from being tracked and alerted.</CustomTooltip>
                                </span>
                            }
                        />
                    </Col>
                </Row>
                <Row>
                    <Col xs={12} className="text-center">
                        <hr />
                        <h5>Alert Notifications</h5>
                    </Col>
                </Row>
                <Row>
                    <Col xs={4} />
                    <Col xs={4} className="text-center">
                        <TextField
                            id={actions.SETTINGS_EMAIL}
                            value={this.props.settings.notification_email}
                            label="Type your email..."
                            margin="normal"
                            variant="standard"
                            type="email"
                            onChange={this.inputChanged}
                            InputProps={{
                                endAdornment: <CustomTooltip>Please enter the email address you would like the alerts sent to.  </CustomTooltip>
                            }}
                            error={this.state.tracking_limit_error}
                            fullWidth
                        />
                    </Col>
                    <Col xs={4} />
                </Row>
                <Row>
                    <Col xs={12} className="text-center">
                        <TrackButton style={{ marginTop: 20, marginBottom: 10 }} color="primary" variant="contained" onClick={this.saveSettings}>
                            {this.state.saving_alert ? <Icon>hourglass_empty</Icon> : ""}
                            {this.props.settings.isSetSettings ? 'UPDATE PRESET RULES' : 'ADD PRESET RULES'} <Icon>notifications_none_alert</Icon>
                        </TrackButton>
                    </Col>
                </Row>
            </Container>
        )
    }
}

const mapStateToProps = state => {
    return {
        track_product: state.track_product,
        settings: state.settings,
        general: state.general,
        progress_bar: state.progress_bar,
        alert: state.alert
    }
}

const mapDispatchToProps = dispatch => {
    return {
        is_set_settings: payload => dispatch({ type: actions.SETTINGS_IS_SET, payload }),
        is_set_tracking: payload => dispatch({ type: actions.TRACK_PRODUCT_IS_SET, payload }),
        set_individual_isbn: payload => dispatch({ type: actions.TRACK_PRODUCT_ISBN, payload }),
        set_individual_price: payload => dispatch({ type: actions.TRACK_PRODUCT_PRICE, payload }),
        set_individual_conditions: payload => dispatch({ type: actions.TRACK_PRODUCT_CONDITION, payload }),
        set_individual_sites: payload => dispatch({ type: actions.TRACK_PRODUCT_SITE, payload }),
        set_individual_description: payload => dispatch({ type: actions.TRACK_PRODUCT_DESCRIPTION, payload }),
        set_individual_seller_name: payload => dispatch({ type: actions.TRACK_PRODUCT_SELLER_NAME, payload }),
        set_individual_seller_rating: payload => dispatch({ type: actions.TRACK_PRODUCT_SELLER_RATING, payload }),
        set_individual_seller_rating_percent: payload => dispatch({ type: actions.TRACK_PRODUCT_SELLER_RATING_PERCENT, payload }),
        set_individual_shipping: payload => dispatch({ type: actions.TRACK_PRODUCT_SHIP_US, payload }),
        set_global_conditions: payload => dispatch({ type: actions.SETTINGS_CONDITION, payload }),
        set_global_sites: payload => dispatch({ type: actions.SETTINGS_SITE, payload }),
        set_global_description: payload => dispatch({ type: actions.SETTINGS_DESCRIPTION, payload }),
        set_global_seller_name: payload => dispatch({ type: actions.SETTINGS_SELLER_NAME, payload }),
        set_global_seller_rating: payload => dispatch({ type: actions.SETTINGS_SELLER_RATING, payload }),
        set_global_seller_rating_percent: payload => dispatch({ type: actions.SETTINGS_SELLER_RATING_PERCENT, payload }),
        set_global_shipping: payload => dispatch({ type: actions.SETTINGS_SHIP_US, payload }),
        set_global_email: payload => dispatch({ type: actions.SETTINGS_EMAIL, payload }),
        set_progress_bar_loading: payload => dispatch({ type: actions.PROGRESS_BAR_LOADING, payload }),
        set_alert_title: payload => dispatch({ type: actions.ALERT_TITLE, payload }),
        set_alert_context_text: payload => dispatch({ type: actions.ALERT_CONTEXT_TEXT, payload }),
        set_alert_open: payload => dispatch({ type: actions.ALERT_OPEN, payload })
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Settings);