import React from 'react';

const actions = {
    ISBN_SET: "isbnst"
}

const initialState = {
    isbn: null
}

const generalReducer = (state=initialState, action) => {
    switch(action.type) {
        case actions.ISBN_SET:
            return {
                ...state,
                isbn: action.payload
            }
        default:
            return state
    }
}

export default generalReducer;
export {
    actions
}