import React from 'react';
import {PriceHistory, TrackProduct, Data, Settings} from '../components/content';

const actions = {
    CONTENT_PRICE_HISTORY: "cntnt1",
    CONTENT_TRACK_PRODUCT: "cntnt2",
    CONTENT_DATA: "cntnt3",
    CONTENT_SETTINGS: "cntnt4"
}
const contentReducer = (state=0, action) => {
    switch(action.type) {
        case actions.CONTENT_PRICE_HISTORY:
            return <PriceHistory />;
        case actions.CONTENT_TRACK_PRODUCT:
            return <TrackProduct />;
        case actions.CONTENT_DATA:
            return <Data />
        case actions.CONTENT_SETTINGS:
            return <Settings />
        default:
            return state;
    }
}

export default contentReducer;
export {
    actions
}