import React, { Component } from 'react';
// import statistics from './statistics.json';
import { connect } from 'react-redux';
import EqualizerIcon from '@material-ui/icons/Equalizer';
import { Container, Col, Row, Table } from 'react-bootstrap';
import './assets/css/style.css';

class Statistics extends Component {
    constructor() {
        super();

        this.state = {
            visible: false
        }
    }
    render() {
        return <div className="statistics-popup-container">
            <div className="statistics-popup-inner" style={{ display: (this.state.visible ? 'inline-block' : 'none') }}>{this.getStatistics()}</div>
            <a href="" onMouseOver={() => {
                this.setState({
                    ...this.state,
                    visible: true
                })
            }} onMouseOut={() => {
                this.setState({
                    ...this.state,
                    visible: false
                })
            }} onClick={(e) => e.preventDefault()} className="statistics-link"><EqualizerIcon style={{ float: 'left' }} /> Statistics</a>
        </div>
    }

    getStatistics() {
        const statistics = this.props.statistics.statistics;

        if (!statistics || !statistics.amazon) {
            return "";
        }

        return (
            <Table striped hover>
                <colgroup>
                    <col />
                    <col style={{ borderLeft: "2px solid orange", background: "rgba(255, 255, 0, 0.1)" }} />
                    <col style={{ borderLeft: "2px solid blue", background: "rgba(0, 0, 255, 0.1)" }} />
                    <col style={{ borderLeft: "2px solid black", background: "rgba(0, 0, 0, 0.1)" }} />
                    <col style={{ borderLeft: "2px solid black", background: "rgba(0, 255, 0, 0.1)" }} />
                </colgroup>
                <thead>
                    <tr>
                        <th></th>
                        <th>Amazon</th>
                        <th>New</th>
                        <th>Used</th>
                        <th>Sales Rank</th>
                    </tr>
                </thead>
                <tbody>
                    <tr className="lowest-text">
                        <td>Lowest</td>
                        <td>
                            <strong>${statistics.amazon.lowest.value}</strong>
                            <p style={{ color: '#aaa', fontSize: '0.8em' }}>{statistics.amazon.lowest.when}</p>
                        </td>
                        <td>
                            <strong>${statistics.new.lowest.value}</strong>
                            <p style={{ color: '#aaa', fontSize: '0.8em' }}>{statistics.new.lowest.when}</p>
                        </td>
                        <td>
                            <strong>${statistics.used.lowest.value}</strong>
                            <p style={{ color: '#aaa', fontSize: '0.8em' }}>{statistics.used.lowest.when}</p>
                        </td>
                        <td>
                            <strong>{statistics.sales_rank.lowest.value}</strong>
                            <p style={{ color: '#aaa', fontSize: '0.8em' }}>{statistics.sales_rank.lowest.when}</p>
                        </td>
                    </tr>
                    <tr className="current-text">
                        <td>Current</td>
                        <td>
                            <strong>${statistics.amazon.current.value}</strong>
                            <p style={{ color: '#aaa', fontSize: '0.8em' }}>{statistics.amazon.current.when}</p>
                        </td>
                        <td>
                            <strong>${statistics.new.current.value}</strong>
                            <p style={{ color: '#aaa', fontSize: '0.8em' }}>{statistics.new.current.when}</p>
                        </td>
                        <td>
                            <strong>${statistics.used.current.value}</strong>
                            <p style={{ color: '#aaa', fontSize: '0.8em' }}>{statistics.used.current.when}</p>
                        </td>
                        <td>
                            <strong>{statistics.sales_rank.current.value}</strong>
                            <p style={{ color: '#aaa', fontSize: '0.8em' }}>{statistics.sales_rank.current.when}</p>
                        </td>
                    </tr>
                    <tr className="highest-text">
                        <td>Highest</td>
                        <td>
                            <strong>${statistics.amazon.highest.value}</strong>
                            <p style={{ color: '#aaa', fontSize: '0.8em' }}>{statistics.amazon.highest.when}</p>
                        </td>
                        <td>
                            <strong>${statistics.new.highest.value}</strong>
                            <p style={{ color: '#aaa', fontSize: '0.8em' }}>{statistics.new.highest.when}</p>
                        </td>
                        <td>
                            <strong>${statistics.used.highest.value}</strong>
                            <p style={{ color: '#aaa', fontSize: '0.8em' }}>{statistics.used.highest.when}</p>
                        </td>
                        <td>
                            <strong>{statistics.sales_rank.highest.value}</strong>
                            <p style={{ color: '#aaa', fontSize: '0.8em' }}>{statistics.sales_rank.highest.when}</p>
                        </td>
                    </tr>
                    <tr className="average-text">
                        <td>Average</td>
                        <td>
                            <strong>${statistics.amazon.average.value}</strong>
                            <p style={{ color: '#aaa', fontSize: '0.8em' }}>{statistics.amazon.average.when}</p>
                        </td>
                        <td>
                            <strong>${statistics.new.average.value}</strong>
                            <p style={{ color: '#aaa', fontSize: '0.8em' }}>{statistics.new.average.when}</p>
                        </td>
                        <td>
                            <strong>${statistics.used.average.value}</strong>
                            <p style={{ color: '#aaa', fontSize: '0.8em' }}>{statistics.used.average.when}</p>
                        </td>
                        <td>
                            <strong>{statistics.sales_rank.average.value}</strong>
                            <p style={{ color: '#aaa', fontSize: '0.8em' }}>{statistics.sales_rank.average.when}</p>
                        </td>
                    </tr>
                    <tr className="drops-text">
                        <td>Drops</td>
                        <td>
                            <strong>{statistics.amazon.drops.value}</strong>
                            <p style={{ color: '#aaa', fontSize: '0.8em' }}>{statistics.amazon.drops.when}</p>
                        </td>
                        <td>
                            <strong>{statistics.new.drops.value}</strong>
                            <p style={{ color: '#aaa', fontSize: '0.8em' }}>{statistics.new.drops.when}</p>
                        </td>
                        <td>
                            <strong>{statistics.used.drops.value}</strong>
                            <p style={{ color: '#aaa', fontSize: '0.8em' }}>{statistics.used.drops.when}</p>
                        </td>
                        <td>
                            <strong>{statistics.sales_rank.drops.value}</strong>
                            <p style={{ color: '#aaa', fontSize: '0.8em' }}>{statistics.sales_rank.drops.when}</p>
                        </td>
                    </tr>
                </tbody>
            </Table>
        );
    }
}

const mapStateToProps = state => ({
    statistics: state.statistics
})

const mapDispatchToProps = dispatch => {
    return {

    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Statistics);