import React, { Component } from 'react';
import {
    Tooltip,
    withStyles,
    Icon
} from '@material-ui/core';

const InfoIcon = withStyles(theme => ({
    root: {
        color: '#C4C4C4',
        float: 'left',
        marginRight: '5px'
    }
}))(Icon);

const LiveHelpIcon = withStyles(theme => ({
    root: {
        color: '#C4C4C4',
        fontSize: '16px !important',
        cursor: 'pointer',
        marginRight: '5px',
        marginTop: '5px',
        marginLeft: '5px'
    }
}))(Icon);

const HtmlTooltip = withStyles(theme => ({
    tooltip: {
      backgroundColor: '#fff',
      fontWeight: 'normal',
      boxShadow: '0px 0px 15px #999',
      color: 'rgba(0, 0, 0, 0.87)',
      maxWidth: 220,
      fontSize: theme.typography.pxToRem(12),
      border: '1px solid #dadde9',
      textAlign: 'justify'
    },
}))(Tooltip);

class CustomTooltip extends Component {
    render () {
        return (
            <HtmlTooltip
                placement='right-start'
                title={
                <React.Fragment>
                    <InfoIcon>info_icon</InfoIcon> {this.props.children}
                </React.Fragment>
                }
            >
                <LiveHelpIcon>live_help_icon</LiveHelpIcon>
            </HtmlTooltip>
        );
    }
}

export default CustomTooltip;