const getDefaultValue = (keyword, defaultValue) => {
    if (!window.localStorage) {
        return defaultValue;
    }

    try {
        var value = JSON.parse(window.localStorage.getItem(keyword));
    } catch (e) {
        var value = window.localStorage.getItem(keyword);
    }

    if (value === null) {
        return defaultValue;
    }

    return value;
}

const setDefaultValue = (keyword, value) => {
    if (!window.localStorage) {
        return;
    }

    window.localStorage.setItem(keyword, value);
}

export {
    getDefaultValue,
    setDefaultValue
}